#body{
  background: #F6F4F4;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

/*nav-bar*/
nav{
  display: flex;
  justify-content: center;
  align-items: center; 
  width: 100vw;
  height: 15vh;
  background-color: #ffffff;
  font-family: 'Inter', sans-serif; 
}

ul{
  list-style-type: none;  
}
#navList{
  display: flex;
  margin-left: 44vw;   
}

li{
  cursor: pointer;
}

.navListItem{
  color: #000000;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  letter-spacing: 0.01em;
  line-height: 24px;
  margin-right: 4vw;     
}

.btn{
  cursor: pointer;
}

.navBtn{
  background: #CD4723;
  color: #ffffff;
  width: 176px;
  height: 48px;
  font-size: 16px;
  border-width: 0;
  border-radius: 10px;
  padding: 12px;
  gap: 10px;
}

/*login and signup*/
.preLanding{
  --color-primary:#009579;
  --color-primary-dark: #007f67;
  --color-secondary: #252c6a;
  --color-error: #cc3333;
  --color-success: #4bb544;
  margin: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  background: #F6F4F4;
}

.form{
  width: 50vw;
  height: 65vh;
  box-shadow: 0px 0px 3px 1px rgba(12, 12, 12, 0.05);
  border-radius: 8px;
  margin: 1rem;
  padding: 2rem;
  font-family: 'Inter', sans-serif;
  background: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.form--hidden{
  height: 45vh;
  width: 50vw;
}

.form> *:last-child {
  margin-bottom: 0;
}

.form__title {
  text-align: center;
  font-family: 'Mulish', sans-serif;
  color: #362A26;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 45px;
  letter-spacing: 0.02em;
  margin-bottom: 2rem;
}

.form__message {
  text-align: center;
  margin-bottom: 1rem;
}

.form__message--success{
  color: var(--color-success);
}

.form__message--error{
  color: var(--color-error);
}

::placeholder {
  color: #BDB4B2;
  opacity: 1;
}

.form__input-group{
  margin-bottom: .5rem;
}

.form__input {
  display: block;
  width: 50vw;
  padding: 0.75rem;
  box-sizing: border-box;
  border-radius: 4px;
  border: 1px solid #dddddd;
  outline: none;
  font-size: 0.9rem;
  background: #ffffff;
  color: #362A26;
  transition: background 0.2s, border-color 0.2s;
}

.form__input:focus{
  border-color: var(--color-primary);
  background: #ffffff;
}

.form__input--error {
  color: var(--color-error);
  border-color: var(--color-error);
}

.form__input-error-message{
  margin-top: 0.5rem;
  font-size: 0.85rem;
  color: var(--color-error);
}

.form__button {
  background: #FFD3C7;
  color: #ffffff;
  font-weight: bold;
  font-size: 1.1rem;
  border: none;
  border-radius: 4px;
  outline: none;
  cursor: pointer; 
  padding: 16px;
  width: 40vw;
  margin-top: 2vh;
}

.form__button:active{
  transform: scale(0.98);
}

.form__text{
  text-align: center;
}

.form__link{
  text-decoration: none;
  cursor: pointer;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.01em;
  color: #362A26;
}

.form__link:hover{
  text-decoration: underline;
}

.first_cta_gp{
  color: #F4602F;
  
}

label{
  color: #71605B;
  display: block;
  margin-bottom: 0.5rem;
}

.label-login{
  margin-bottom: 2vh;
}

.bids{
  margin-top: 10vh;
  font-family: 'Inter', sans-serif;
}

.bids-header{
  text-align: center;
  font-style: normal;
  font-weight: 700;
  font-size: 64px;
  line-height: 72px;
  letter-spacing: 0.02em;
  color: #362A26;
}

.ongoing-span{
  color: #CD4723;
}

.bids-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.bids-list{
  display: flex;
}

.bids-list-items{
  width: 26vw;
  height: 50vh;
  margin-right: 1vw;
  margin-bottom: 5vh;
  background: #FDF8F7;
  border-radius: 16px 16px 0px 0px;
}

.ongoing-img{
  width: 15vw;
  margin: auto;
}

.img-container{
  width: 26vw;
  height: 30vh;
  display: flex;
  background: #E2E9E8;
  border-radius: 16px 16px 0px 0px;
}

.product-name{
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.01em;
  margin-bottom: 0;
}

.product-price{
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: 0.01em;
  color: #362A26;
  margin: 2vh 0 2vh 0;
}

.bids-deets{
  margin-right: 1vw;
  display: flex;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.01em;
  color: #71605B;
}

.bids-bids , .bids-timer{
  margin-right: 1vw;
}

.bids-btn{
  font-weight: 400;
  background: #2F4644;
  border-radius: 5px;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  letter-spacing: 0.01em;
  border-width: 0;
  color: #FFFFFF;
  margin-right: 1vw;
}

.buy-now{
  background: #FDF8F7;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  text-align: center;
  letter-spacing: 0.01em;
  border: 1px solid #2F4644;
  color: #71605B;
}

.see-more{
  width: 176px;
  height: 48px;
  background: #CD4723;
  margin-top: 5vh;
  font-size: 16px;
  font-weight: 400;
  border-radius: 10px;
  border-width: 0;
  line-height: 24px;
  letter-spacing: 0.01em;
  color: #FFFFFF;   
}



.easy::after{
  margin-right: 2vw;
  content:i;   
}

#footerContainer{
  background-color: #000000;
  margin-top: 20vh;
  padding: 15vh;
  border-bottom-right-radius: 250px;
  height: 65vh;
  font-family: 'Inter', sans-serif;
}

#footer-div{
  width: 70vw;
  margin: auto;
  color: #ffffff;
}
#footer-contact{
  margin-bottom: 15vh;
  font-size: 50px;
  font-style: normal;
  font-weight: 400;
  line-height: 72px;
  letter-spacing: 0.02em;
  color: #F1F4F4;
  cursor: pointer;
}

#footer-second-div{
  display: flex;
  margin-top: 15vh;
}

.footer-list{
  margin-right: 5vw;
}

.footer-list-header{
  text-align: left;
  margin-bottom: 7vh;
  font-weight: 400;
font-size: 20px;
line-height: 30px;
/* or 150% */

letter-spacing: 0.05em;

/* Neutrals/300 */

color: #71605B;
}

.footer-list-list{
  text-align: left;
  width: 12vw;
  font-weight: 400;
font-size: 16px;
line-height: 26px;
/* identical to box height, or 162% */

letter-spacing: 0.05em;
}

#hero--section{
  display: flex;
  margin-left: 3vw;
  margin-top: 6vh;
  font-family: 'Mulish', sans-serif;
}

#left--side-hero{
  width: 15vw;
  height: 65vh;
  margin-right: 10vh;
  padding-bottom: 16px;
}

#hero--section-list{
  width: 15vw;
  height: 65vh;
  margin-top: 0;
  padding: 16px 24px;
  background-color: #ffffff;
}

.hero--section-list-list{
  margin-bottom: 4vh;
}

#right--side--hero{
  display: flex;
  flex-direction: column;
  width: 70vw;   
}

#all-categories-container{
  display: flex;
  background-color: #ffffff;
  margin-bottom: 7vh;
}

.right--side--hero-img{
  width:70vw;
  height: 55vh;
}

#hero--btn{
  background: #2F4644;
  width: 200px;
  height: 48px;
  padding: 12px;
  border-radius: 0px 10px 10px 0;
}

#all-categories-search{
  flex-grow: 2;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 20px;
  letter-spacing: 0.01em;
  color: #000000;
  padding: 12px;
  width: 55vw;
  height: 24px;
  background: #FFFFFF;
  border-radius: 4px 0px 0px 4px;
  border-width: 0;
}
#all-categories-search:focus{
  outline: 0;
}

select{
  font-family: 'Inter', sans-serif;   
  border-width: 0;
}

select:focus{
  outline: 0;
}
.select{
  width: 1px;
  background-color: #BDB4B2;
  border: 0px;
  height: 6vh;
  margin: auto;
}





















