#heroSection{
    margin-left: 10vw;
    font-family: 'Mulish', sans-serif;
}
  
#leftSideHero{
    width: 40vw;
    margin-top: 15vh;
}
  
h1{ 
    font-size: 44px;
    font-weight: 700;
    line-height: 53px;
    letter-spacing: 0.02em;
}
  
#heroBtn{
    background: #2F4644;
    width: 176px;
    height: 56px;
    margin-left: 1vw;
    padding: 12px;
    border-radius: 10px;
    gap: 10px;   
    border-width: 0;
    color: #ffffff; 
}
  
#heroArticle{
    margin-bottom: 15vh;
}

#searchContainer{
    display: flex;
    width: 60vw;
    border-radius: 10px;
}
  #searchBox{
    display: flex;
    border: 0;
    background-color: #ffffff;
    width: 60vw;
    border-radius: 10px;
}
  
#allCategoriesSearch{
    flex-grow: 2;
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 20px;
    letter-spacing: 0.01em;
    color: #000000;
    padding: 12px;
    width: 12vw;
    height: 24px;
    background: #FFFFFF;
    border-radius: 10px;
    border-width: 0;
}
  
#allCategoriesSearch:focus{
    outline: 0;
}

#categoriesDiv{
    display: flex;
    width: 12vw;
    margin-right: 3vw;
    margin-left: 1vw;
    border-radius: 10px;
   
}

select{
    border-radius: 10px;
}

#demacator {
    width: 1px;
    background-color: #BDB4B2;
    border: 0px;
    height: 6vh;
    margin: auto;
}
  
#heroSubSection{
    margin-top: 30vh;
    font-family: 'Inter', sans-serif;
}
  
#heroSubSectionHeader{
    text-align: center;
    font-size: 64px;
    font-weight: 700;
    font-style: normal;
    line-height: 72px;
    letter-spacing: 0.02em;
    color: #362A26;
}
  
#promo{
    width: 80vw;
    height: 90vh;
    background: linear-gradient(90.8deg, #E3EFF4 9.08%, #F7EADC 66.11%);
    margin: auto;
    margin-top: 20vh;
    margin-left: 10vw;    
}

#promoContainer{
    display: flex;
    flex-direction: column;
}

.promoArticleDiv{
    width: 578px;
    height: 90px;
}

#promoArticle{
    width: 578px;
    height: 90px;
    margin-left: 10vw;
    margin-top: 7vh;
    font-family: 'inter', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 7vw;
    line-height: 100px;
    letter-spacing: 0.02em;
    color: #EB5757;
}

.promoCtaContainer{
    display: flex;
    flex-direction: row;
    margin-top: 20vh;
    margin-left: 7vw;
}

.promoImgContainer{
    width: 35vw;
    background-color: red;
    height: 55vh;
    margin-right: 20vw;
}

.promoCta{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 17vh;
}

.promoDiscount{
    font-family: 'inter', sans-serif;
font-style: normal;
font-weight: 700;
font-size: 32px;
line-height: 72px;
/* or 225% */

letter-spacing: 0.02em;

/* Primary/200 */

color: #CD4723;
}

.promoBtn{
    display: flex;
flex-direction: row;
justify-content: center;
align-items: center;
padding: 12px;
gap: 10px;

width: 176px;
height: 48px;

/* Secondary/100 */

background: #202E2D;
border-radius: 4px;
font-family: 'Inter', sans-serif;
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 24px;
/* identical to box height, or 150% */

letter-spacing: 0.01em;

color: #FFFFFF;
}

#categories{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    margin-top: 10vh;
}
  
#categoriesHeader{
    text-align: center;
    font-size: 64px;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 700; 
    line-height: 72px;
    letter-spacing: 0.02em;
    color: #362A26;
}
  
  #categoriesContainer{ 
    width: 50vw;
    margin-left: -33vw;
}
  
.categoriesList{
    display: flex;
    width: 80vw;
    margin-bottom: 3vh;   
}
  
.categoriesListItems{
    width: 26vw;
    height: 35vh;
    background-color: #FDF8F7;
    margin-right: 1vw;
    padding-bottom: 2vh;
    border-radius: 16px;
    border: 1px solid #C6D2D1;
    cursor: pointer;
}
  
.categoriesListImg{
    width: 26vw;
    height: 25vh;
    border-radius: 16px 16px 0px 0px;
}
  
.categoriesListTitle{
    text-align: center;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 32px;
    letter-spacing: 0.01em;
    color: #71605B;
}
  
